let Model = {}
Model.config = {}

let model = new Proxy({}, {
  get (target, key, receiver) {
    try {
      return require(`../model/${key}_model`)
    } catch (e) {
      console.error(e)
    }
  }
})

Model.install = function (Vue, options) {
  Vue.prototype.$model = model
}
module.exports = Model



